	module.exports = {"data":{"markdownRemark":{"html":"<p><em>Credit: Trad/Rich Soni</em></p>\n<p>When I bought that house and ship, I was young and full of zip,<br>\nleft my my friends and family back in old Bayonne.<br>\nAnd so I made the race, and I staked me out a place<br>\nAnd settled down along a tidal zone  </p>\n<p><em>It flowed away, it flowed away</em><br>\n<em>My little Seaside home it flowed away</em><br>\n<em>I was living on a prayer when I built my shanty there.</em><br>\n<em>Everything except my mortgage flowed away</em>  </p>\n<p>I got this broken boat, and I fixed it up to float<br>\nAimed to have Sawmill slice to feed my face<br>\nWhen I was done I tied the bow to the dock outside the house<br>\nAnd I knew that I had finally found my place  </p>\n<p><em>It flowed away, it flowed away</em><br>\n<em>That ship that I fixed up flowed away</em><br>\n<em>When the Atlantic had its panic it went down like The Titanic</em><br>\n<em>All the pieces of my ship they flowed away</em>  </p>\n<p>My banana stand, lost to the roving sand<br>\nThe stock is probably down in old VA<br>\nAll the cash inside sailed down Route 35<br>\nMy Banana  shop in seaside flowed away  </p>\n<p><em>It flowed away, it flowed away</em><br>\n<em>Everything I owned flowed away</em><br>\n<em>I hollered and I cussed when my stand went with a thrust</em><br>\n<em>When my business that I own, it flowed away</em>  </p>\n<p>I was living on a prayer when I built my shanty there.<br>\nI figured I was all set for all my life.<br>\nSo up hopped into my woody and went looking for a goody<br>\nBut, I found myself a puggle, and not a wife  </p>\n<p><em>He flowed away, he flowed away</em><br>\n<em>My Jersey Shore pup he flowed away</em><br>\n<em>I leaned on down to pet him, but I saw that he could swim</em><br>\n<em>My jersey shore puggle flowed away</em>  </p>\n<p>Then I was left alone a-listenin' to the moan<br>\nOf the sea around the corners of my shack<br>\nSo I took off down the road when the ocean water flowed<br>\nA-travelin' with the waves cracking at my back  </p>\n<p><em>I flowed away, I flowed away</em><br>\n<em>Mantoloking bridge was up ahead</em><br>\n<em>I was living on a prayer as I saw my shanty tear</em><br>\n<em>My house its settled in the ocean bed</em>  </p>\n<p>Now I'm always close to home no matter where I roam,<br>\nFor those waters spread all across the land<br>\nMakes no difference where I'm walkin', I can hear my ship a-rockin'<br>\nI can hear that puggle a-barkin' in the air  </p>\n<p><em>It flowed away,  it flowed away</em><br>\n<em>My little seaside home it flowed away</em><br>\n<em>But my home is always near; its in the waters clear</em><br>\n<em>My little seaside home it flowed away</em>  </p>\n<p>I'm a writ of right New Jersyite, but we don't need to reunite<br>\nAnd Ill never get homesick 'til I die.<br>\nNo matter where I'm found, my home is all around;<br>\nMy seaside home is wherever it rains, or water lies  </p>\n<p><em>It flowed away, it flowed away,</em><br>\n<em>My shanty down at exit 82</em><br>\n<em>But all around the world, wherever sand is whirled</em><br>\n<em>Some is what my seaside home broke into</em>  </p>\n<p><em>It flowed away, it flowed away,</em><br>\n<em>My little seaside home it flowed away</em><br>\n<em>Its over in the sea where the waves are rolling free</em><br>\n<em>My little seaside home it flowed away</em>  </p>","frontmatter":{"title":"Little Seaside Home"},"fields":{"date":null}}},"pathContext":{"slug":"/songs/little-seaside-home/"}}

